$glowColor: #0b2dc4;

@keyframes glow {
  0% {
    text-shadow: 0 0 2.5px #fff, 0 0 5px #fff, 0 0 7.5px lightblue, 9 9 10px lightblue;
  }
  100% {
    text-shadow: 0 0 10px #fff, 0 0 7.5px $glowColor, 0 0 10px $glowColor;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}